import React from 'react'
import FancyCard from './ui/FancyCard';

export default function Tools() {
    return (
        <div className="tools">
            <FancyCard title="Adobe XD" image="assets/icons/xd.png" css="background-image: linear-gradient( 30.5deg,  rgba(190,70,102,0.93) -15.6%, rgba(252,154,154,1) -15.6%, rgba(190,70,102,0.92) 17.9%, rgba(58,13,48,1) 81.6% );"/>
            <FancyCard title="Photoshop CC" image="assets/icons/photoshop.png" css="background-image: linear-gradient( 65.5deg,  rgba(23,205,205,1) -15.1%, rgba(23,25,95,1) 71.5% );"/>
            <FancyCard title="npm & yarn" image="assets/icons/npm.png" css="background-image: radial-gradient( circle farthest-corner at 50.4% 50.5%,  rgba(251,32,86,1) 0%, rgba(135,2,35,1) 90% );"/>
            <FancyCard title="Visual Studio Code" image="assets/icons/vsc.png" css="background-image: linear-gradient( 0.3deg,  rgba(30,161,239,1) 0.9%, rgba(49,46,252,1) 88.6% );"/>
            <style jsx>{`
            .tools{
                padding: 0 5%;
                display: flex;
                flex-direction: rows;
                flex-wrap: wrap;
                justify-content: spece-evenly;
            }
            `}</style>
        </div>
    )
}
